.navigation {
  padding: 0 10px;

  & .navigation-list {
    width: max-content;
    display: flex;
    margin-left: auto;
    margin-right: 40px;

    & .nav-item {
      padding: 10px;
      margin: 0 5px;
      border-radius: 5px;
      transition: background-color 0.3s;

      &:hover {
          background-color: var(--primary);
      }

      &:hover .nav-link {
        color: var(--onPrimary);
      }

      & .nav-link {
        text-decoration: none;
        color: var(--onSurface);
        font-size: 1.2rem;
      }
    }
  }
}
